import React from 'react';
import {Card, CardTitle, CardText, CardFooter} from 'reactstrap';

import renderHTML from 'react-render-html';
import Moment from 'react-moment';

const MakaleCard = props => {
  return (
    <Card className="mt-5">
      <CardTitle className="lead article-title text-center">{renderHTML(props.articleTitle)}</CardTitle>
      <div className="container">{renderHTML(props.articleTitle)}</div>
      <CardFooter>Yayın Tarihi: <Moment format="YYYY/MM/DD" date={props.articleDate} /></CardFooter>
      <button onClick={() => console.log(new Date())}>Tıkla</button>
    </Card>
  )
}

export default MakaleCard;
